
.hero {
    display: flex;
    flex-direction: column;
    width: 100vw;
    min-height: 100vh;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-image: linear-gradient(
      15deg,
      rgba(179, 179, 179, 0.808),
      rgba(59, 59, 59, 0.815)
    );
    color: white;
    position: relative;
    overflow: hidden;
  }
  .title{
    font-size: 45px;
  }
  .logodog{
    width: 350px;
  }
  .video_bg {
    position: absolute;
    z-index: -1;
    top: 60%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 180%;
    min-height: 150%;
  }
  
  .bubblyButton {
      display: inline-block;
      font-size: 2em;
      padding: .5em .5em;
      margin-top: 60px;
      margin-bottom: 60px;
      -webkit-appearance: none;
      appearance: none;
      background-color: #3d3d3dc5;
      color: #fff;
      border-radius: 4px;
      border: none;
      cursor: pointer;
      position: relative;
      transition: transform ease-in 0.1s, box-shadow ease-in 0.25s;
      box-shadow: 0 2px 25px rgba(202, 202, 202, 0.5);
    }
    .bubblyButton:focus {
      outline: 0;
    }
    .bubblyButton:before, .bubblyButton:after {
      position: absolute;
      content: "";
      display: block;
      width: 140%;
      height: 100%;
      left: -20%;
      z-index: -1000;
      transition: all ease-in-out 0.5s;
      background-repeat: no-repeat;
    }
      .bubblyButton:active {
      transform: scale(0.9);
      background-color: #023809;
      box-shadow: 0 2px 25px rgba(2, 122, 32, 0.2);
    }
    .bubblyButton.animate:before {
      display: block;
      animation: topBubbles ease-in-out 0.75s forwards;
    }
  